import parsePhoneNumber from 'libphonenumber-js';

/**
 * Formatea un nmero de telfono para WhatsApp usando el formato E.164
 *
 * WhatsApp acepta nmeros en formato E.164 CON el smbolo '+':
 * - USA: +13056345678
 * - Colombia: +573001234567
 * - Mxico: +525512345678
 *
 * @param phone - Nmero de telfono en cualquier formato
 * @returns Nmero formateado para WhatsApp (E.164 con '+')
 *
 * @example
 * formatPhoneForWhatsApp('+1 305-634-5678') // '+13056345678'
 * formatPhoneForWhatsApp('+57 300 123 4567') // '+573001234567'
 * formatPhoneForWhatsApp('13056345678') // '+13056345678'
 */
export function formatPhoneForWhatsApp(phone: string): string {
  if (!phone) return '';

  try {
    // Intentar parsear el nmero de telfono
    const phoneNumber = parsePhoneNumber(phone);

    if (phoneNumber && phoneNumber.isValid()) {
      // Retornar formato E.164 CON el '+': +12133734253
      return phoneNumber.number;
    }

    // Si el nmero no es vlido pero tiene el formato correcto, intentar con diferentes pases
    // Esto maneja casos donde react-phone-input-2 ya guard el nmero con cdigo de pas
    if (phone.startsWith('+')) {
      // Ya tiene +, solo limpiar caracteres no numricos excepto el +
      return '+' + phone.substring(1).replace(/\D/g, '');
    }

    // Si el nmero ya tiene cdigo de pas (sin +), verificar si comienza con cdigos conocidos
    const cleanNumber = phone.replace(/\D/g, '');
    if (cleanNumber.startsWith('1') ||
        cleanNumber.startsWith('52') ||
        cleanNumber.startsWith('57') ||
        cleanNumber.startsWith('54') ||
        cleanNumber.startsWith('55') ||
        cleanNumber.startsWith('56') ||
        cleanNumber.startsWith('58') ||
        cleanNumber.startsWith('51') ||
        cleanNumber.startsWith('50')) {
      return '+' + cleanNumber;
    }

    // Si no tiene cdigo de pas, retornar el nmero limpio con +
    return '+' + cleanNumber;

  } catch (error) {
    console.error('Error parsing phone number:', error);

    // Fallback: agregar + si no lo tiene
    const cleanNumber = phone.replace(/\D/g, '');
    return phone.startsWith('+') ? '+' + cleanNumber : '+' + cleanNumber;
  }
}

/**
 * Formatea un nmero de telfono para mostrar en formato internacional legible
 *
 * @param phone - Nmero de telfono en cualquier formato
 * @returns Nmero formateado para mostrar (ej: '+1 213 373 4253')
 *
 * @example
 * formatPhoneForDisplay('+12133734253') // '+1 213 373 4253'
 * formatPhoneForDisplay('573001234567') // '+57 300 123 4567'
 */
export function formatPhoneForDisplay(phone: string): string {
  if (!phone) return '';

  try {
    const phoneNumber = parsePhoneNumber(phone);

    if (phoneNumber && phoneNumber.isValid()) {
      return phoneNumber.formatInternational();
    }

    return phone;
  } catch (error) {
    console.error('Error formatting phone for display:', error);
    return phone;
  }
}
