import React, { useState } from "react";
import { useAuth } from "../../contexts/AuthContext";
import { Button } from "../../components/ui/button";
import { Input } from "../../components/ui/input";
import { Label } from "../../components/ui/label";
import { toast } from "sonner"; // Asumiendo que sonner est instalado para notificaciones

const ResellerSettingsPage: React.FC = () => {
    const { user, updatePassword } = useAuth(); // Asumiendo que updatePassword existe en AuthContext
    const [newPassword, setNewPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");
    const [isUpdating, setIsUpdating] = useState(false);

    const handlePasswordChange = async (e: React.FormEvent) => {
        e.preventDefault();
        if (newPassword !== confirmPassword) {
            toast.error("Las contraseas no coinciden.");
            return;
        }
        if (newPassword.length < 6) {
            // Supabase default min password length
            toast.error("La contrasea debe tener al menos 6 caracteres.");
            return;
        }

        setIsUpdating(true);
        try {
            // Aqu llamaras a la funcin de Supabase para actualizar la contrasea
            // Por ahora, simulamos una llamada
            // await supabase.auth.updateUser({ password: newPassword });
            // O si tienes una funcin en tu AuthContext:
            await updatePassword(newPassword);
            toast.success("Contrasea actualizada exitosamente.");
            setNewPassword("");
            setConfirmPassword("");
        } catch (error) {
            console.error("Error al cambiar la contrasea:", error);
            toast.error("Error al cambiar la contrasea. Intntalo de nuevo.");
        } finally {
            setIsUpdating(false);
        }
    };

    return (
        <div className="p-4">
            <h1 className="text-2xl font-bold mb-4">
                Configuracin del Revendedor
            </h1>

            <div className="bg-card p-6 rounded-lg shadow-md mb-6">
                <h2 className="text-xl font-semibold mb-4">
                    Datos de la Cuenta
                </h2>
                <p>
                    <strong>Email:</strong> {user?.email}
                </p>
                {/* Aqu podras aadir ms datos del usuario si estn disponibles en el objeto user */}
            </div>

            <div className="bg-card p-6 rounded-lg shadow-md">
                <h2 className="text-xl font-semibold mb-4">
                    Cambiar Contrasea
                </h2>
                <form onSubmit={handlePasswordChange} className="space-y-4">
                    <div>
                        <Label htmlFor="new-password">Nueva Contrasea</Label>
                        <Input
                            id="new-password"
                            type="password"
                            value={newPassword}
                            onChange={(e) => setNewPassword(e.target.value)}
                            required
                            className="mt-1"
                        />
                    </div>
                    <div>
                        <Label htmlFor="confirm-password">
                            Confirmar Contrasea
                        </Label>
                        <Input
                            id="confirm-password"
                            type="password"
                            value={confirmPassword}
                            onChange={(e) => setConfirmPassword(e.target.value)}
                            required
                            className="mt-1"
                        />
                    </div>
                    <Button
                        type="submit"
                        disabled={isUpdating}
                        className="w-full"
                    >
                        {isUpdating ? "Cambiando..." : "Cambiar Contrasea"}
                    </Button>
                </form>
            </div>
        </div>
    );
};

export default ResellerSettingsPage;
